.headerHomePage {
  position: relative;
  width: 100%;
}
.headerButton {
  display: none;
}
.mars3d {
  height: 60vh;
}

@media screen and (min-width: 700px) {
  .mars3d {
    height: 70vh;
  }

  .headerButton {
    background-color: #13103cbb;
    border-radius: 10px;
    color: #fff;
    display: flex;
    flex-flow: column wrap;
    padding: 1rem;
    position: absolute;
    top: 70%;
    left: 80%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }
  .headerButton h1 {
    font-weight: 700;
    font-size: 1.3rem;
    margin: 0 0 1.5rem 0;
  }
  .headerButton button {
    background-color: #fff;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    display: inline-block;
    padding: 0.8rem 0.5rem;
    transition: all 300ms ease;
    width: 120px;
  }
  .headerButton button:hover {
    background-color: #4a486ebb;
    color: #fff;
  }
  .headerButton a {
    align-items: flex-end;
    color: rgb(27, 27, 27);
    font-size: 0.9rem;
    font-family: sans-serif;
    text-decoration: none;
  }
}
