.roverPictureh2 {
  font-size: 1.8rem;
  font-family: 'Rajdhani', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}
.pictures-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 1rem;
}
.formSearchPicture {
  display: flex;
  flex-flow: column wrap;
}
.limitPictureLabel {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  width: 100%;
}
form span {
  margin-right: 0.5rem;
}
