.container {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.homeMessage {
  padding: 1rem 1rem 2rem 1rem;
}
.homeMessage h2 {
  padding: 0.5rem;
  font-size: 2.2rem;
  text-align: center;
}
.homeMessage p {
  font-size: 16px;
  width: 100%;
}
.homeMessage p em {
  display: inline-block;
  font-style: italic;
}

@media screen and (min-width: 800px) {
  .homeMessage p {
    margin: 1rem auto;
    width: 60%;
  }
}
